*, *::after, *::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    /* font-size: 50.5%; */
    font-family: 'Open Sans', sans-serif;
}

body {
    font-family: inherit;
}

/* root variables */

:root {
    --white: #ffffff;
    --darker-white: #eeeeee;
    --light-black: #222222;

    --light-pink: #9383C8;
    --pink: #ea74ff8a;

    --dark-purple: #090622;
    --transparent-purple: #090622da;
    --light-purple: #0D0831;

    --yellow: #EAD471;
    --cyan: #00CEFF;
    --shadow: rgba(0, 0, 0, .4)
}

.app {
    height: 100vh;
    width: 100%;
    background-image: linear-gradient(to bottom right, var(--light-pink), var(--pink)), url(https://images.pexels.com/photos/53621/calculator-calculation-insurance-finance-53621.jpeg);
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .calculator {
    height: 80vh;
    width: 25%;
    box-shadow: 0 2rem 4rem var(--shadow);
    background-color: var(--light-black);
  }

  .result-screen {
    height: 15vh;
    font-size: 4.5rem;
    font-weight: 300;
    color: var(--white);
    /* To display the value at the bottom-right of the container */
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    text-shadow: 0 0 10px var(--cyan);
  }

  .computation-screen {
    height: 10vh;
    font-size: 1.5rem;
    color: var(--yellow);
    /* To display the value at the bottom-right of the container */
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
  }

  .screen {
    height: 25vh;
    width: 100%;
    padding: 0 2rem;
    overflow: hidden;
    white-space: no-wrap;
    background-color: var(--dark-purple);
  }

  .keypad-row {
    height: 11vh; /* (80vh calculator - 25vh screen) / 5 keypadRow components = 11vh */
    width: 100%;
    display: flex; /* To control the dimension */
  }

  .btn:last-child {
      color: var(--yellow);
  }

  .keypad {
    height: 55vh;
    width: 100%;
  }

  .btn {
    width: 25%;
    height: 100%;
    /* font-size: 3rem; */
    font-family: inherit;
    border: none;
    background-color: var(--light-purple);
    color: var(--white);
    font-size: 26px;
    font-weight: 100;
    /* border: 1px solid transparent; */
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .2s;
  }

  .btn:hover {
    color: var(--white);
    background-color: var(--dark-purple);
  }

  .btn:active {
    transform: scale(.95);
    text-shadow: 0 0 14px var(--cyan);;
  }

/* Media Queries */

@media only screen 
  and (max-device-width: 414px)
  and (-webkit-min-device-pixel-ratio: 2) {
    .calculator {
      height: 80vh;
      width: 90%;
  }

  .result-screen {
    font-size: 3.7rem;
  }

  .computation-screen {
    font-size: 1.9rem;
  }
  
}

/* Landscape */
@media only screen 
  and (max-device-width: 823px) 
  and (orientation: landscape) { 
    
    .calculator {
      height: 80vh;
      width: 70%;
  }

  .result-screen {
    font-size: 2.2rem;
  }

  .computation-screen {
    font-size: 1rem;
  }

}


/* iPad Portrait */
@media only screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1024px) 
  and (orientation: portrait) 
  and (-webkit-min-device-pixel-ratio: 2) {

    .calculator {
      height: 80vh;
      width: 70%;
  }

  }

  /* iPad pro portrait */

  @media only screen 
  and (min-device-width: 834px) 
  and (max-device-width: 834px) 
  and (orientation: portrait) 
  and (-webkit-min-device-pixel-ratio: 2) {

    .calculator {
      height: 80vh;
      width: 70%;
  }

}





